<template>
  <div class="home-page">
    <div class="home-container">
      <img src="../assets/images/home_bg@2x.png" />
      <div class="home-button">
        <button class="start-button" @click="startPhoto">上传照片</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    startPhoto () {
      this.$router.push('/choosephoto')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.home-page {
  width: 100%;
  height: 100%;
  .home-container {
    width: 100%;
    height: 100%;
    line-height: 0;
    img {
      position: relative;
      width: 100%;
      height: 100%;
      z-index: -999;
    }
    .home-button {
      position: absolute;
      bottom: 65px;
      text-align: center;
      width: 100%;
      // .start-button {
      // }
    }
  }
}
</style>



