// 微信相关的方法
const wx = window.wx || {}

export const initWechatJs = (appId, nonceStr, timestamp, signature, uploadCallback) => {
  wx.config({
    debug: false,
    appId: appId,
    timestamp: timestamp,
    nonceStr: nonceStr,
    signature: signature,
    jsApiList: [
      'checkJsApi',
      'chooseImage',
      'uploadImage',
      'onMenuShareTimeline'
    ]
  })
}

export const openCameraForPic = (type, uploadCallback) => {
  var sourceType = []
  if (type === 1) {
    sourceType.push('album')
  } else {
    sourceType.push('camera')
  }
  wx.chooseImage({
    count: 1, // 默认9
    sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
    sourceType: sourceType, // 可以指定来源是相册还是相机，默认二者都有
    success: function (res) {
      var localIds = res.localIds // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
      uploadCallback.call(this, localIds)
    }
  })
}



