<template>
  <div id="app">
    <transition name="fade" mode="in-out">
      <router-view/>
    </transition>
  </div>
</template>

<script>
// import wx from 'weixin-js-sdk'
import {initWechatJs} from '@/common/wechat'

export default {
  name: 'App',
  mounted () {
    this.initWechatJs()
  },
  methods: {
    initWechatJs () {
      console.log(111)
      this.axios.post('/api/webchat-api/pub/signature').then((res) => {
        if (res.code === 200) {
          var wechatSign = res.data
          // wx.config({
          //   debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
          //   appId: wechatSign.appId, // 必填，公众号的唯一标识
          //   timestamp: wechatSign.timestamp, // 必填，生成签名的时间戳
          //   nonceStr: wechatSign.nonceStr, // 必填，生成签名的随机串
          //   signature: wechatSign.signature,// 必填，签名
          //   jsApiList: [
          //     chooseImage,
          //     updateTimelineShareData
          //   ] // 必填，需要使用的JS接口列表
          // });
          initWechatJs(wechatSign.appId, wechatSign.timestamp, wechatSign.nonceStr, wechatSign.signature)
        }
      }).catch((response) => {
        console.log(response)
      })
    }
  }
}
</script>

<style lang='scss'>
html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    width: 100%;
    height: 100%;
    .fade-enter {
      opacity:0;
    }
    .fade-leave{
      opacity:1;
    }
    .fade-enter-active{
      transition:opacity .5s;
    }
    .fade-leave-active{
      opacity:0;
      transition:opacity .5s;
    }
  }
}
button {
  border: 0;
  width: 160px;
  height: 50px;
  line-height: 50px;
  background-color: #000000;
  color: #ffffff;
  border-radius: 50px;
}
</style>



